export default{
    getWechat(c,h,o){
		var link = "https://wx.codeclassroom.org/activity/index.php?parent_openid=" + o + "&centre_id=" + c + "&hdid=" + h;
		var uurl = location.href.split('#')[0];
		$.ajax({
			type: "post",
			url: "https://hdapi.codeclassroom.org/index/hd/shares",
			dataType: "json",
			async: false,
			data: {
				url: uurl
			},
			success: function(data) {
				wx.config({
					debug: false, //生产环境需要关闭debug模式
					appId: data.result.appId, //appId通过微信服务号后台查看
					timestamp: data.result.timestamp, //生成签名的时间戳
					nonceStr: data.result.nonceStr, //生成签名的随机字符串
					signature: data.result.signature, //签名
					jsApiList: [ //需要调用的JS接口列表
						'checkJsApi', //判断当前客户端版本是否支持指定JS接口
						'onMenuShareTimeline', //分享给好友
						'onMenuShareAppMessage', //分享到朋友圈
						'getLocation',//获取当前地理位置
					]
				});
			}
		})
		wx.ready(function() {
			var protocol = window.location.protocol;
			//分享朋友圈
			$.ajax({
				"url": "https://hdapi.codeclassroom.org/index/hd/fenx",
				type: "post",
				dataType: 'json',
				data: {
					centre_id: c,
					hd_id: h
				},
				success: function(data) {
					var imgurl = data.data.img
					var intro = data.data.fx_describe
					var share_title = data.data.title
		
					wx.onMenuShareTimeline({
						title: share_title,
						desc: intro, // 分享描述
						link: link,
						imgUrl: imgurl, // 自定义图标
						trigger: function(res) {
							// 不要尝试在trigger中使用ajax异步请求修改本次分享的内容，因为客户端分享操作是一个同步操作，这时候使用ajax的回包会还没有返回.
						},
						success: function(res) {
							$.ajax({
								"url": "https://hdapi.codeclassroom.org/index/hd/fxgzt",
								type: "post",
								dataType: 'json',
								data: {
									openid: o,
									centre_id: c,
									hd_id: h
								},
								success: function(data) {
									
								}
							});
						},
						cancel: function(res) {
							//alert('shared cancle');
						},
						fail: function(res) {
							//alert(JSON.stringify(res));
						}
					});
					//分享给好友
					wx.onMenuShareAppMessage({
						title: share_title,
						desc: intro, // 分享描述
						link: link,
						imgUrl: imgurl, // 自定义图标
						type: 'link', // 分享类型,music、video或link，不填默认为link
						dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
						success: function(res) {
							// 用户确认分享后执行的回调函数
							$.ajax({
								"url": "https://hdapi.codeclassroom.org/index/hd/fxgzt",
								type: "post",
								dataType: 'json',
								data: {
									openid: o,
									centre_id: c,
									hd_id: h
								},
								success: function(data) {
									
								},
								error: function(e) {
									
								}
							});
						},
						cancel: function(res) {
							// 用户取消分享后执行的回调函数
						},
						fail: function(res) {
							//alert(JSON.stringify(res));
						}
					});
				}
			});
			wx.getLocation({
				type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
				success: function(res) {
					var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
					var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
					var speed = res.speed; // 速度，以米/每秒计
					var accuracy = res.accuracy; // 位置精度
		
					$.ajax({
						"url": "https://hdapi.codeclassroom.org/index/hd/check_user_address",
						type: "post",
						dataType: 'json',
						data: {
							openid: o,
							hd_id: h,
							latitude: latitude,
							longitude: longitude
						},
						success: function(data) {
							
						}
					});
				},
				error: function(e) {
					alert(e.errMsg);
				},
				cancel: function(res) {
					alert('用户拒绝授权获取地理位置');
				}
			});
			wx.checkJsApi({
				jsApiList: [
					'getLocation'
				],
				success: function (res) {
					if (res.checkResult.getLocation == false) {
						alert('你的微信版本太低，不支持微信JS接口，请升级到最新的微信版本！');
					}
				}
			}); 
			wx.error(function(res) {
				alert('出现意外错误:' + res.errMsg);
			});
		});
	}
}